.pagebody {
  height: 100vh;
  align-items: center;
  justify-content: center;
  --tw-bg-opacity: 1;
  background-color: rgba(15, 20, 36, var(--tw-bg-opacity));
  font-family: Rubik;
}

@media (min-width: 1024px){
  .pagebody{
    display: flex;
  }
}

.main-container {
  /* lg-py */
  gap: 1.75rem;
  --tw-bg-opacity: 1;
  background-color: rgba(15, 20, 36, var(--tw-bg-opacity));
  padding-left: 5%;
  padding-right: 5%;
  padding-top: 1.75rem;
  padding-bottom: 1.75rem;
  padding-bottom: 1rem;
}

@media (min-width: 640px){
  .main-container{
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-template-rows: repeat(4, minmax(0, 1fr));
  }
}

@media (min-width: 1024px){
  .main-container{
    grid-template-columns: repeat(4, minmax(0, 13rem));
    grid-template-rows: 13rem 13rem;
  }
}

.time-categories {
  width: 100%;
  top: 7rem;
  background-color: #1c1f4a;
  position: absolute;
  display: flex;
  height: 5rem;
  width: 100%;
  align-items: center;
  justify-content: space-around;
  border-radius: 0.75rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  padding-top: 1rem;
}

@media (min-width: 640px){
  .time-categories{
    padding-top: 1.5rem;
  }
}

@media (min-width: 1024px){
  .time-categories{
    top: 18rem;
    height: 10rem;
    flex-direction: column;
    align-items: stretch;
    padding-top: 3.5rem;
    padding-bottom: 1rem;
  }
}

.account-container {
  position: relative;
  grid-column: span 2 / span 2;
  margin-bottom: 1.5rem;
  height: 12rem;
  width: 100%;
}

@media (min-width: 640px){
  .account-container{
    margin-bottom: 0px;
  }
}

@media (min-width: 1024px){
  .account-container{
    grid-column: span 1 / span 1;
    grid-row: span 2 / span 2;
    height: 24rem;
    width: 12rem;
  }
}

@media (min-width: 1024px) {
  .account-container {
    height: 28rem;
  }
}

.person-info {
  position: relative;
  z-index: 10;
  margin-left: 0px;
  display: flex;
  height: 8rem;
  width: 100%;
  border-radius: 0.75rem;
  --tw-bg-opacity: 1;
  background-color: rgba(87, 70, 243, var(--tw-bg-opacity));
  padding: 2rem;
}

@media (min-width: 1024px){
  .person-info{
    height: 20rem;
    flex-direction: column;
  }
}

.person-img {
  margin-right: 1.25rem;
  height: 4rem;
  width: 4rem;
  border-radius: 9999px;
  border-width: 2px;
  --tw-border-opacity: 1;
  border-color: rgba(255, 255, 255, var(--tw-border-opacity));
}

@media (min-width: 640px){
  .person-img{
    margin-right: 2.25rem;
  }
}

@media (min-width: 1024px){
  .person-img{
    margin-bottom: 1.75rem;
  }
}

@media (min-width: 510px) {
  .person-info p span {
    margin-bottom: 0.25rem;
  }

  .person-info p {
    font-size: 2.25rem;
    line-height: 2.5rem;
  }
}

.person-info p {
  font-size: 1.7rem;
  margin-top: 0.5rem;
  padding-left: 0.5rem;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 300;
  --tw-text-opacity: 1;
  color: rgba(255, 255, 255, var(--tw-text-opacity));
}

@media (min-width: 640px){
  .person-info p{
    margin-top: 0px;
    font-size: 2.25rem;
    line-height: 2.5rem;
  }
}

@media (min-width: 768px){
  .person-info p{
    font-size: 3.75rem;
    line-height: 1;
  }
}

@media (min-width: 1024px){
  .person-info p{
    font-size: 2.25rem;
    line-height: 2.5rem;
  }
}

.person-info p span {
  display: block;
  padding-right: 1.25rem;
  font-size: 0.75rem;
  line-height: 1rem;
  --tw-text-opacity: 1;
  color: rgba(156, 163, 175, var(--tw-text-opacity));
}

@media (min-width: 640px){
  .person-info p span{
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
}

.time-category {
  cursor: pointer;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 700;
  letter-spacing: 0.1em;
  --tw-text-opacity: 1;
  color: rgba(156, 163, 175, var(--tw-text-opacity));
}

.time-category:hover{
  --tw-text-opacity: 1;
  color: rgba(255, 255, 255, var(--tw-text-opacity));
}

.time-category{
  transition-property: background-color, border-color, color, fill, stroke;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  transition-timing-function: cubic-bezier(0, 0, 0.2, 1);
}

.active-time-category {
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 700;
  letter-spacing: 0.1em;
  --tw-text-opacity: 1;
  color: rgba(239, 246, 255, var(--tw-text-opacity));
}

.card {
  position: relative;
  margin-bottom: 1.25rem;
  height: 10rem;
  --tw-text-opacity: 1;
  color: rgba(255, 255, 255, var(--tw-text-opacity));
}

@media (min-width: 640px){
  .card{
    margin-bottom: 0px;
  }
}

.card-color-bg {
  position: absolute;
  height: 8rem;
  width: 100%;
  overflow: hidden;
  border-radius: 0.75rem;
}

.card-color-bg img {
  position: absolute;
  top: -0.75rem;
  right: 0.75rem;
  width: 4rem;
}

.card .card-main {
  position: absolute;
  top: 2.25rem;
  height: 8rem;
  width: 100%;
  border-radius: 0.75rem;
  --tw-bg-opacity: 1;
  background-color: rgba(28, 31, 74, var(--tw-bg-opacity));
}

.card .card-main:hover{
  --tw-bg-opacity: 1;
  background-color: rgba(52, 57, 123, var(--tw-bg-opacity));
}

.card .card-main{
  padding: 1.25rem;
  transition-property: background-color, border-color, color, fill, stroke;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  transition-timing-function: cubic-bezier(0, 0, 0.2, 1);
}

@media (min-width: 640px){
  .card .card-main{
    height: 10rem;
  }
}

@media (min-width: 1024px){
  .card .card-main{
    height: 11rem;
  }
}

.card .card-head {
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (min-width: 1024px){
  .card .card-head{
    margin-bottom: 1rem;
  }
}

.card .card-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (min-width: 1024px){
  .card .card-content{
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
  }
}

.card .card-content .hrs {
  margin-bottom: 0.75rem;
  font-size: 3rem;
  line-height: 1;
  font-weight: 300;
}

@media (min-width: 640px){
  .card .card-content .hrs{
    font-size: 3.75rem;
    line-height: 1;
  }
}

.card .card-content .last-week-report {
  font-size: 0.75rem;
  line-height: 1rem;
  --tw-text-opacity: 1;
  color: rgba(156, 163, 175, var(--tw-text-opacity));
}

@media (min-width: 640px){
  .card .card-content .last-week-report{
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
}

.z-10{
  z-index: 10;
}

.table{
  display: table;
}

.h-1{
  height: 0.25rem;
}

.w-full{
  width: 100%;
}

@keyframes spin{
  to{
    transform: rotate(360deg);
  }
}

@keyframes ping{
  75%, 100%{
    transform: scale(2);
    opacity: 0;
  }
}

@keyframes pulse{
  50%{
    opacity: .5;
  }
}

@keyframes bounce{
  0%, 100%{
    transform: translateY(-25%);
    animation-timing-function: cubic-bezier(0.8,0,1,1);
  }

  50%{
    transform: none;
    animation-timing-function: cubic-bezier(0,0,0.2,1);
  }
}

.cursor-pointer{
  cursor: pointer;
}

